.itemkol {
  max-width: 25%; 
}
.pagekol {
  display: flex;margin-top: 4px;
  flex-direction: column;
  height: 100%;
}
.textholder {
  height: 35%;
}
.itemsholder {
  height: 50%;
}
.textkhli {
  overflow-y: auto;
margin: 0 auto;
  color: #dff2ff;
  width: 97%;
  height: 100%; 
}
.item1 {
  margin: 0 10px;
  overflow-y: auto;
  height: 80%;
}

.farsi {
  direction: rtl;
  margin-top: 1rem;
  margin-bottom: 3rem;
}
.button{
 /* background-color: none; */
 cursor: pointer;display: inline-block;vertical-align:middle;
}
.savechanges{
  display: flex;
  align-items: center;
 justify-content: space-between;
}
.savechanges h1{
  margin: 0.5rem 0rem;
}
 
.savebutton{
  background-color: #00aeff;
  margin-left: 1rem;
 font-weight: bold;font-size: 1.5rem;
  padding: 0.5rem 2rem;border: none;
  border-radius: 1rem;cursor: pointer;
  color: white;
}
@media only screen and (min-width: 900px) {
 
  .pagekol {
    display: flex;
margin-top: 20px;
    flex-direction: row-reverse;
    height: 100%;
  }
  .textholder {
    height: 100%;
    width: 100%;
  }
  .itemsholder {
    width: 100%;
    height: 100%;
  }
  .item1 {
    /* margin: 1rem auto; */
    margin-top: 5px;
    width: 90%;
    height: 80%;
  }
  .textkhli {
    /* margin: 1rem auto; */
    color: #dff2ff;
    /* width: 90%; */
    height: 80%;
    font-size: 1.3rem;
  }
  .savechanges{
   
   justify-content: flex-start;
  }
 
}

 
@media only screen and (min-width: 2000px) {
  .textkhli {
    font-size: 1.5rem;
  }
}

