.itemview{
    display: flex;
    align-items: center;
    background-color: #00aeff;
    justify-content: space-between;
   cursor: pointer;
    margin-top: 5px;
    padding: 5px;
    border-radius: 15px;
    font-family: Verdana, blutosbold, Tahoma, sans-serif;
}
.overf{
    overflow-x: auto;
    /* width: 210px; */
    flex-wrap: nowrap;
    padding-left: 5px;
}
.jadokme{
    display: flex;
    flex-wrap: nowrap;
}
.jadokme button {
    font: inherit;
    cursor: pointer;
    background-color: #024b9e;
    border: none;
    color: white;
    padding: 0.5rem 1rem;
    border-radius: 10px;
    margin: 2px 2px;
    flex-wrap: nowrap;
    min-width: 6rem;
  }